<template>
  <section class="section">
    <transition-group name="universal" tag="div">
      <div class="notification is-primary" v-if="isDataAdd" key="note">
        <button class="delete" @click="isDataAdd = !isDataAdd"></button>
        <span>Изменения внесены</span>
      </div>
    </transition-group>

    <div class="container">
      <nav class="navbar" role="navigation" aria-label="main navigation">
        <div class="navbar-brand">
          <a class="navbar-item" href="/">
            <h1 class="title is-3">person.json app</h1>
          </a>

          <a
            role="button"
            class="navbar-burger burger"
            aria-label="menu"
            aria-expanded="false"
            data-target="navbarPersonApp"
            @click.prevent="isNavActive = !isNavActive"
          >
            <span aria-hidden="true"></span>
            <span aria-hidden="true"></span>
            <span aria-hidden="true"></span>
          </a>
        </div>

        <transition-group
          name="navbar"
          tag="div"
          appear
          class="navbar-menu-wrap"
        >
          <div
            class="navbar-menu is-active"
            id="navbarPersonApp"
            key="navbar"
            v-if="isNavActive"
          >
            <div class="navbar-end" key="navbar-end">
              <div class="navbar-item">
                <div class="buttons">
                  <a class="button is-info is-medium" @click="viewModeEdit">
                    <strong>Переключить вид</strong>
                  </a>
                  <a class="button is-primary is-medium" @click="activated">
                    <strong>Создать запись</strong>
                  </a>
                </div>
              </div>
            </div>
          </div>
        </transition-group>
      </nav>

      <transition-group name="universal" tag="div">
        <div class="modal is-active" v-if="isActive || isEditActive" key="form">
          <div class="modal-background" @click="closeModal" key="bg"></div>
          <div class="modal-content container" key="content">
            <div class="card">
              <div class="card-content">
                <div class="content">
                  <form
                    action=""
                    @submit.prevent="onSubmit"
                    v-if="!isEditActive"
                  >
                    <h2 class="has-text-left">Создать запись</h2>
                    <div
                      class="field"
                      v-for="item in metaList"
                      :key="item.name"
                    >
                      <label class="label has-text-left">{{ item.name }}</label>
                      <div class="control">
                        <input
                          v-model="formData[item.name]"
                          class="input"
                          :type="typeValidation(item.name)"
                          :required="
                            item.name != 'CARDTEMPLNAME' ? true : false
                          "
                          :placeholder="item.name"
                        />
                      </div>
                    </div>

                    <div class="field is-grouped buttons">
                      <div class="control">
                        <button class="button is-primary" type="submit">
                          Создать
                        </button>
                      </div>
                      <div class="control">
                        <button
                          class="button is-danger is-light"
                          @click.prevent="resetForm"
                        >
                          Очистить форму
                        </button>
                      </div>
                    </div>
                  </form>

                  <form
                    action=""
                    v-if="isEditActive"
                    @submit.prevent="onSubmitEdit(editRowPerson)"
                  >
                    <h2 class="has-text-left">Редактировать запись</h2>
                    <div
                      class="field"
                      v-for="(item, name) in editRowPerson"
                      :key="name"
                    >
                      <label class="label has-text-left">{{ name }}</label>
                      <div class="control">
                        <input
                          v-model="editRowPerson[name]"
                          class="input"
                          :type="typeValidation(name)"
                          :required="name != 'CARDTEMPLNAME' ? true : false"
                          :placeholder="name"
                        />
                      </div>
                    </div>

                    <div class="field is-grouped buttons">
                      <div class="control">
                        <button class="button is-primary" type="submit">
                          Создать
                        </button>
                      </div>
                      <div class="control">
                        <button
                          class="button is-danger is-light"
                          @click.prevent="resetForm"
                        >
                          Очистить форму
                        </button>
                      </div>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
          <button
            @click="closeModal"
            class="modal-close is-large"
            aria-label="close"
          ></button>
        </div>
      </transition-group>
    </div>
  </section>
</template>

<script>
import { eventBus } from "../main";

export default {
  name: "MainForm",
  data: () => ({
    formData: {},
    isActive: false,
    isDataAdd: false,
    editRowPerson: false,
    editPersonIndex: "",
    isNavActive: true
  }),
  computed: {
    isEditActive: function() {
      if (this.editRowPerson != false) {
        return true;
      } else {
        return false;
      }
    }
  },
  props: {
    metaList: {
      type: Array
    }
  },
  methods: {
    typeValidation(name) {
      if (
        name === "CARD" ||
        name === "LASTNAME" ||
        name === "FIRSTNAME" ||
        name === "PATRONYMIC" ||
        name === "STATUSNAME" ||
        name === "CARDTEMPLNAME" ||
        name === "ORGNAMESHORT" ||
        name === "PERSONTYPENAME" ||
        name === "CURRNAME" ||
        name === "DOCTYPENAME" ||
        name === "BIRTHDATE"
      ) {
        return "text";
      } else {
        return "number";
      }
    },
    onSubmit() {
      const newFormData = { ...this.formData };
      this.$emit("onSubmit", newFormData);
      this.resetForm();
      this.isActive = false;
      this.dataAdd();
    },
    onSubmitEdit(formData) {
      const newFormData = { ...formData };
      const index = this.editPersonIndex;
      this.$emit("onSubmitEdit", newFormData, index);
      this.closeModal();
      this.dataAdd();
    },
    activated() {
      this.isActive = true;
    },
    resetForm() {
      this.formData = {};
      Object.keys(this.editRowPerson).forEach(key => {
        this.editRowPerson[key] = null;
      });
    },
    dataAdd() {
      this.isDataAdd = true;
      setTimeout(() => (this.isDataAdd = false), 1800);
    },
    closeModal() {
      this.isActive = false;
      this.editRowPerson = false;
    },
    viewModeEdit() {
      this.$emit("viewModeEdit");
    }
  },
  created() {
    eventBus.$on("editRowPerson", (editRowPerson, index) => {
      this.editRowPerson = { ...editRowPerson };
      this.editPersonIndex = index;
    });
    if (window.innerWidth <= 1030) {
      this.isNavActive = false;
    }
  }
};
</script>

