<template>
  <section class="section">
    <div class="container">
      <transition-group
        name="table"
        tag="div"
        appear
        class="table-container"
        v-if="viewMode"
      >
        <table class="table is-bordered" key="table">
          <thead>
            <tr>
              <th>Удалить</th>
              <th>Редактировать</th>
              <th v-for="item in metaList" :key="item.name">
                {{ item.name }}
              </th>
            </tr>
          </thead>
          <transition-group name="table" tag="tbody" appear>
            <tr
              v-for="(person, index) in personList"
              :key="person.CARD + index"
            >
              <td>
                <a @click.prevent="editRow(index)">Редактировать</a>
              </td>
              <td><a @click.prevent="deleteRow(index)">Удалить</a></td>
              <td v-for="item in person" :key="item">
                {{ item }}
              </td>
            </tr>
          </transition-group>
        </table>
      </transition-group>

      <transition-group
        name="table"
        tag="div"
        appear
        class="columns-wrapper"
        v-else
      >
        <div
          class="others"
          v-for="(person, index) in personList"
          :key="person.CARD + index"
        >
          <div class="card">
            <div class="card-content has-text-left">
              <ul v-for="(item, name) in person" :key="item">
                <li>
                  <b>{{ name }}</b
                  >: {{ item }}
                </li>
              </ul>
            </div>
          </div>
        </div>
      </transition-group>
    </div>
  </section>
</template>

<script>
import { eventBus } from "../main";

export default {
  name: "MainTable",
  props: {
    personList: {
      type: Array
    },
    metaList: {
      type: Array
    },
    viewMode: {
      type: Boolean
    }
  },
  methods: {
    editRow(index) {
      eventBus.$emit("editRowPerson", this.personList[index], index);
    },
    deleteRow(index) {
      this.$emit("deleteRow", index);
    }
  }
};
</script>

