<template>
  <div id="app">
    <MainForm
      :metaList="metaList"
      @onSubmit="addRow"
      @onSubmitEdit="editRow"
      @viewModeEdit="viewMode = !viewMode"
    />
    <MainTable
      :personList="personList"
      :metaList="metaList"
      @deleteRow="deleteRow"
      :viewMode="viewMode"
    />
  </div>
</template>

<script>
import MainTable from "@/components/MainTable.vue";
import MainForm from "@/components/MainForm.vue";

export default {
  name: "app",
  components: {
    MainTable,
    MainForm
  },
  data: () => ({
    personList: [],
    metaList: [],
    editForm: {},
    viewMode: true
  }),
  computed: {},
  methods: {
    editRow(formData, index) {
      this.$set(this.personList, index, formData);
    },
    deleteRow(index) {
      this.personList.splice(index, 1);
    },
    addRow(formData) {
      this.personList.push(formData);
    }
  },
  created() {
    (async () => {
      const response = await fetch(this.$json).then(response =>
        response.json()
      );
      const fullData = response.data;
      this.metaList = Array.from(Object.values(fullData.metaData));

      const objGen = rowIndex => {
        return new Promise(resolve => {
          const result = {};
          Array.from(Object.values(fullData.metaData)).forEach((el, index) => {
            result[el.name] = fullData.rows[rowIndex][index];
          });
          resolve(result);
        });
      };

      fullData.rows.forEach(async (el, index) => {
        let personObj = await objGen(index);
        this.personList.push(personObj);
      });
    })();
  }
};
</script>

<style lang="scss">
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
</style>
